@use 'base/variables' as v;
@use 'base/mixins' as m;
.partners_section{
    background: linear-gradient(rgba(0, 0, 0, .7), rgba(0, 0, 0, 1));
    padding: 8rem 0 12rem;
    .partners_title{
        margin: 0;
        text-align: center;
        text-transform: uppercase;
        padding: 0 2rem 8rem;
        
        @include m.tablet {
            padding: 4rem 0 10rem;
            
        }
    }
    .partners_div{
        display: flex;
        overflow: hidden; // Oculta el desbordamiento de las palabras
        gap: 1rem;


            .partner{
                display: inline-block;
                animation: moveLeft 15s linear infinite; // Aplica la animaci√≥n a cada palabra
                padding: 3rem;
                border-radius: 2rem;
                background-color: lighten(v.$black, 7%);
                min-width: 15rem;
                align-content: center;

                @include m.tablet {
                    min-width: 30rem;
                }

                img{
                    width: 100%;
                }
            }

    }
    @keyframes moveLeft {
        0% { transform: translateX(5%);} // Inicia desde la derecha
        100% { transform: translateX(-958.3%);} // Se desplaza hacia la izquierda
    }

    @include m.tablet {
        @keyframes moveLeft {
            0% { transform: translateX(5%);} // Inicia desde la derecha
            100% { transform: translateX(-928.3%);} // Se desplaza hacia la izquierda
        }
    }
}