// Fonts
$font_main: 'Inter', sans-serif;
$font_variant: 'Exo', sans-serif;

// Media Queries Sizes
$mobile: 480px;
$tablet: 768px;
$desktop: 1025px;

// Colors
$dark: #091f32; // 35%
$lightgreen: #6bb334; // 15%
$green: #3b652c; // 10%
$aqua: #6bc1b6; // 5%

//auxiliaries
$lightgrey: #dadada; 
$white: #FFFFFF; //25%
$black: #000;
$greyBlue: #E8F0FE;

// alternate colors - avoid using them so much
$yellow: #F59E0B;
$lightgold: #FAD961;
$gold: #AB9144;
$darkgold: #92792f;
$orangeFlame: #E4572E;


// Font Weights
$lightest: 100;
$light: 200;
$regular: 400;
$semibold: 600;
$bold: 700;
$boldest: 900;
