@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    .bigS_nav{
        display: none;

        @include m.tablet {
            display: flex;
            flex-direction: column;
            height: 0;
            transition: all .3s ease;
            position: fixed;
            z-index: 999;
            width: 100%;

            .main_nav{
                padding: 2rem 5rem;
                display: flex;
                justify-content: space-between;
                align-items: center;
                .left{
                    display: block;
                    img{
                        width: 16rem;
                    }
                }
                .right{
                    display: flex;
                    flex-direction: row;
                    gap: 5rem;
                    .nav{
                        gap: 2rem;
                        display: flex;
                        a, p{
                            align-content: center;
                            color: v.$white;
                            font-size: 2rem;
                            transition: transform .3s ease;
        
                            &:hover{
                                transform: scale(1.1);
                                cursor: pointer;
                            }
                        }
                    }
                    a{
                        align-content: center;
                        img{
                            vertical-align: middle;
                            width: 4rem;
                            &:hover{
                                cursor: pointer;
                            }
                        }
                    }
                }

            }
            .mobile_nav{
                @include m.tablet {
                    display: none;
                };
            }
        }
    }
    // Here starts the mobile nav code
    .mobile_nav{
        position: fixed;
        width: 100%;
        z-index: 999;
        .up{
            display: flex;
            height: 12rem;
            background-color: transparent;
            margin: 0;
            padding: 0 2rem;
            justify-content: space-between;
            align-content: center;
            a{
                align-content: center;
                img{
                    width: 12rem;
                }
            }
    
            .expand_nav_btn, .collapse_nav_btn{
                width: 5rem;
            }

            .expand_nav_btn{
                display: none;
                &.show_btn{
                    display: block;
                }
            }

            .collapse_nav_btn{
                display: none;
                &.show_btn{
                    display: block;
                }
            }
        }

        @include m.tablet {
            display: none;
        };
        .mobile_nav_expanded {
            opacity: 0;
            overflow: hidden;
            height: 0;
            background-color: rgba(v.$black,  .9);
            transition: opacity 0.3s ease;
            overflow-y: hidden; /* Agregar esta l√≠nea */
        }
        
        &.mostrar {
            .mobile_nav_expanded {
                opacity: 1;
                position: fixed;
                z-index: 99;
                inset: 0;
                height: 100%; /* Cambiar height: 100% a height: 100vh */
                opacity: 1;
                .nav {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    margin-top: 10rem;
                    a, p {
                        color: v.$white;
                        font-size: 2.4rem;
                        margin: 1.5rem 0;
                    }
                }
                .down{
                    margin-top: 10rem;
                    display: flex;
                    flex-direction: column;

                    align-items: center;
                    gap: 2rem;
                    img{
                        width: 7rem;
        
                        &:hover{
                            cursor: pointer;
                        }
                    }
                }
            }
        }
    }
}