@use 'base/variables' as v;
@use 'base/mixins' as m;

.home{
    .hero{
        background-image: linear-gradient(rgb(0, 0, 0), rgba(32, 32, 32, 0.7)),
        url(../../build/gif/lock.gif);
        background-size: cover;
        background-position: center center;
        height: 100vh;
        max-width: 100%;
        align-content: center;
        @include m.tablet {
            align-content: end;
        }
        
        .hero_content{
            h1, h3, p{
                margin: 0;
                margin-left: 0;
                text-align: center;
                @include m.tablet {
                    margin-left: 2rem;
                    text-align: start;
                }
                @include m.desktop {
                    margin-left: 3rem;
                }
            }

            h1 {
                padding: 0;
                font-size: 6rem;
                line-height: .8;
                letter-spacing: -.2rem;
                @include m.tablet {
                    font-size: 14rem; 
                    letter-spacing: -.5rem;
                }
                @include m.desktop {
                    font-size: 20rem; 
                    letter-spacing: -1rem;
                }
                color: v.$white;
                font-weight: v.$bold;
                max-width: 100%;
                text-transform: uppercase;
                gap: 1rem;
            }
            h3{
                font-size: 1.2rem;
                @include m.tablet {
                    font-size: 2.4rem; 
                    margin-left: 5rem;
                }
                @include m.desktop {
                    font-size: 3.2rem;
                    margin-left: 8rem; 
                }
            }
            p{
                text-transform: uppercase;
                color: v.$black;
                background-color: rgba(v.$white, .4);
                font-size: 1.4rem;

                @include m.tablet {
                    font-size: 1.5rem;
                    padding: 0 1rem;
                    width: 28rem;
                    margin-left: 5rem;
                    margin-bottom: 3rem;
                }
                @include m.desktop {
                    font-size: 1.8rem;
                    margin-left: 8rem;
                    width: 35rem;
                }
            }
        }
    }
    
    .intro{
        padding: 1rem;
        @include m.tablet {
            padding: 2rem;
            
        }
        background-image: linear-gradient(rgba(0, 0, 0, .2), rgba(0, 0, 0, 0.6)),
        url(../../build/img/gradient1.webp);
        background-size: cover;
        background-position: center center;
        max-width: 100%;
        align-content: center;

        .intro_title{
            text-align: center;
            margin: 20rem 0rem;
            text-transform: uppercase;
            
            @include m.tablet {
                margin: 20rem 5rem;
                
            }
        }
        .intro_div{
            display: flex;
            margin: 12rem 0;

            .intro_empty{
                width: 100%;
            }
            .intro_text{
                display: flex;
                .text_icono{
                    align-content: center;
                    @include m.tablet {
                        display: flex;
                        gap: 1rem;
                        flex: 0 0 40%;
                        justify-content: center;
                        
                    }

                    img{
                        width: 12rem;
                        @include m.tablet {
                            width: 18rem;
                            
                        }
                    }
                    p{
                        align-content: center;
                        font-size: 1.2rem;
                        margin: 0;
                        line-height: 1;
                        text-align: center;
                        span{
                            text-align: start;
                            font-size: 3rem;
                            font-weight: v.$bold;
                            @include m.tablet {
                                font-size: 4rem;
                                
                            }
                        }
                    }
                }
                .text_description{
                    flex: 0 0 65%;
                    @include m.tablet {
                        flex: 0 0 50%;
                    }
                    @include m.desktop {
                        flex: 0 0 55%;
                    }
                    padding: 1rem 1.5rem;
                    border-radius: 2rem;
                    background: linear-gradient(90deg, lighten(v.$black, 14%) 20%, lighten(v.$black, 12%) 40%, lighten(v.$black, 10%) 80%);
                    @include m.tablet {
                        padding: 1rem 3rem;
                    }
                    h3{
                        font-size: 2rem;
                        @include m.tablet {
                            font-size: 3rem;
                        }
                    }
                    p{
                        font-size: 1.2rem;
                        @include m.tablet {
                            font-size: 1.4rem;
                        }
                        @include m.desktop {
                            font-size: 1.6rem;
                        }
                    }
                }
            }
            .text_left, .text_right{
                display: flex;
                align-content: center;
                justify-content: space-between;

            }

            .text_left{
                text-align: start;
            }

            .text_right{
                text-align: end;
            }

        }
        .end{
            justify-content: flex-end;
        }
    }
    .us_section{
        background: linear-gradient(rgba(0, 0, 0, .6), rgba(0, 0, 0, 1));
        padding: 2rem 1rem;
        @include m.tablet {
            padding: 6rem 3rem;
        }
        img{
            display: block;
            width: 20rem;
            margin: 0 auto;
            margin-bottom: 1rem;
        }
        .us_title{
            margin: 0;
            text-align: center;
            margin-bottom: 6rem;
            text-transform: uppercase;
        }
        .us_div{
            .us_text{
                @include m.tablet {
                    padding: 0 5rem;
                }
                p{
                    text-align: center;
                }
            }
        }
        .button_trnsp{
            margin-top: 5rem;
            @include m.button(transparent, v.$white, v.$white);
            @include m.tablet {
                width: 50%;
                margin: 5rem auto 0;
            }
        }
    }
    .services_section{
        background-image: linear-gradient(rgba(0, 0, 0, .3), rgba(0, 0, 0, 0.8)),
        url(../../build/img/gradient2.webp);
        background-size: cover;

        max-width: 100%;
        align-content: center;
        padding: 5rem 1rem;
        @include m.tablet {
            padding: 15rem 3rem;
        }
        .services_title{
            margin: 0;
            text-align: center;
            margin-bottom: 10rem;
            text-transform: uppercase;
        }
        .services_div{
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 3rem 1rem;
            @include m.tablet {
                grid-template-columns: repeat(3, 1fr);
            }
            .service{
                transition: transform .3s ease;
                display: block;
                .service_title{
                    text-align: center;
                    margin: 0;
                }
                .service_icon{
                    width: 10rem;
                    height: 10rem;
                    display: block;
                    margin: 0 auto;
                    fill: v.$white;
                }
                &:hover{
                    transform: scale(1.2);
                }
            }
        }
        .button_trnsp{
            margin-top: 5rem;
            @include m.button(transparent, v.$white, v.$white);
            @include m.tablet {
                width: 50%;
                margin: 10rem auto 0;
            }
        }
    }
}