@use 'base/variables' as v;
@use 'base/mixins' as m;

.us{
    .hero{
        background-image: linear-gradient(rgb(0, 0, 0), rgba(32, 32, 32, 0.7)),
        url(../../build/gif/team.gif);
        background-size: cover;
        background-position: center center;
        height: 100vh;
        max-width: 100%;
        align-content: center;
        @include m.tablet {
            align-content: end;
        }
        
        .hero_content{
            img{
                display: block;
                margin: auto;
                width: 90%;
                @include m.tablet {
                    width: 80%;
                    margin-left: 3rem;
                    margin-bottom: 3rem;
                }
            }
        }
    }
    .us_section{
        h3{
            font-size: 2.2rem;
            text-align: center;
            @include m.tablet {
                font-size: 3rem;
            }
        }
        padding: 1rem;
        @include m.tablet {
            padding: 8rem 3rem;
        }
        background-image: linear-gradient(rgba(0, 0, 0, .2), rgba(0, 0, 0, 0.6)),
        url(../../build/img/gradient1.webp);
        background-size: cover;
        background-position: center center;
        max-width: 100%;
        align-content: center;
        .us_main_div{
            margin: 10rem 0;
            @include m.tablet {
                margin: 0 auto 10rem;
                width: 60%;
            }
            .us_title{
                text-align: center;
            }
            .us_text{
                p, h3{
                    text-align: center;
                    @include m.tablet {
                        text-align: start;
                    }
                }
            }
        }
        .us_split_div{
            @include m.tablet {
                display: flex;
            }
            justify-content: space-between;
            .mision, .vision{
                background: linear-gradient(90deg, lighten(v.$black, 14%) 20%, lighten(v.$black, 12%) 40%, lighten(v.$black, 10%) 80%);
                flex: 0 0 45%;
                height: 100%;
                padding: 2rem;
                border-radius: 1rem;
                margin-bottom: 1rem;
                @include m.tablet {
                    height: 45vh;
                }
                @include m.desktop {
                    height: 30vh;
                    
                }
            }
        }
        .us_last_div{
            margin: 10rem 0;
            .values{
                @include m.tablet {
                    display: flex;
                    
                }
                gap: .5rem;

                details{
                    background: linear-gradient(90deg, lighten(v.$black, 14%) 20%, lighten(v.$black, 12%) 40%, lighten(v.$black, 10%) 80%);
                    padding: 2rem;
                    text-align: center;
                    color: v.$lightgrey;
                    margin: .5rem auto;
                    width: 100%;
                    border-radius: .5rem;
                    align-content: center;

                    summary{
                        margin: 1rem 0;
                        font-weight: v.$bold;

                        &:hover{
                            cursor: pointer;
                        }
                    }
                }
            }
        }
    }
}