@use 'variables' as v;
@use 'base/mixins' as m;

h1, h2, h3 {
    font-family: v.$font_main;
    font-weight: v.$bold;
}
h1 {
    font-size: 4.4rem;
    text-align: center;
    margin: 5rem 0 2rem;
    color: v.$white;
    @include m.tablet {
        font-size: 7.2rem;
        margin: 0 0 5rem;
        padding-top: 20rem;
    }
}
h2 {
    font-size: 4rem;
    @include m.tablet {
        font-size: 5rem;
    }
}
h3 {
    font-size: 3rem;
    @include m.tablet {
        font-size: 4rem;
    }
}
h4 {
    font-size: 2.2rem;
    
    @include m.tablet {
        font-size: 3rem;
    }
}
p {
    font-size: 1.4rem;
    color: darken(v.$lightgrey, 15%);
    line-height: 1.8;
    @include m.tablet {
        font-size: 1.6rem;
    }
}

h1, h2, h3 {
    color: v.$white;
}
h4, p, li{
    color: v.$lightgrey;
}

.tagline {
    
    @include m.tablet {

    };
}

