@use 'base/variables' as v;
@use 'base/mixins' as m;

.contact{
    .hero{
        background-image: linear-gradient(rgb(0, 0, 0), rgba(32, 32, 32, 0.7)),
        url(../../build/gif/contact.gif);
        background-size: cover;
        background-position: center center;
        height: 100vh;
        max-width: 100%;
        align-content: center;
        @include m.tablet {
            align-content: end;
        }
        
        .hero_content{
            display: flex;
            gap: .5rem;
            justify-content: center;
            @include m.tablet {
                justify-content: start;
                margin: 0 0 3rem 3rem;
            }
            img{
                display: block;
                margin: 0;
                width: 15%;
                @include m.tablet {
                    width: 15%;
                }
            }
            h1{
                margin: 0;
                text-align: center;
                padding: 0;
                font-size: 6rem;
                line-height: .8;
                letter-spacing: -.2rem;
                color: v.$white;
                font-weight: v.$bold;
                max-width: 100%;
                text-transform: uppercase;
                align-content: center;
                @include m.tablet {
                    margin: auto 0;
                    text-align: start;
                    font-size: 10rem; 
                    letter-spacing: -.5rem;
                }
                @include m.desktop {
                    font-size: 15rem; 
                    letter-spacing: -1rem;
                }
            }
        }
    }
    .contact_section{
        background-image: linear-gradient(rgba(0, 0, 0, .3), rgba(0, 0, 0, 0.8)),
        url(../../build/img/gradient2.webp);
        background-size: cover;

        max-width: 100%;
        align-content: center;
        padding: 5rem 1rem;
        @include m.tablet {
            padding: 15rem 3rem;
        }
        .contact_title{
            margin: 0;
            text-align: center;
            margin-bottom: 10rem;
            text-transform: uppercase;
        }
        p{
            text-align: center;
            @include m.tablet {
                width: 70%;
                margin: 0 auto 2rem;
            }
        }
        .form {
            width: 100%;
            @include m.tablet {
                width: 70%;
                margin: auto;
            }
            .field {
                display: flex;
                align-items: center;
                margin-bottom: 2rem;
                label {
                    flex: 0 0 10rem;
                    font-size: 1.4rem;
                    color: v.$lightgrey;
                    @include m.tablet {
                        flex: 0 0 12rem;
                        font-size: 1.7rem;
                    }
                    &.wider{
                        flex: 0 0 8rem; 
                    }
                }
                input, select, textarea {
                    flex: 0;
                    min-width: 70%; // esto hace que en celulares los inputs no tomen mas tama√±o del asignado
                    padding: 1rem;
                    border: none;
                    background-color: v.$greyBlue;
                    border: solid 0.2rem lighten(v.$lightgrey, 46%);
                    border-radius: .5rem;
                    font-size: 1.4rem;
                    color: v.$black;
                    @include m.tablet {
                        flex: 1;
                        font-size: 1.7rem;
                    };
                    &.custom-file-input {
                        padding: .4rem;
                      }
                }
                textarea {
                    height: 12rem;
                }
                select{
                    cursor: pointer;
                    appearance: none;
                    background-color: v.$greyBlue;
                    color: v.$black;
                    &::-ms-expand{
                        display: none;
                    }
                }
            }
            .custom-file-input::-webkit-file-upload-button {
                visibility: hidden;
              }
              .custom-file-input::before {
                content: 'Elegir fotos';
                display: inline-block;
                background-color: lighten(v.$lightgreen, 10%);
                border: 0.2rem solid lighten(v.$white, 0%);
                border-radius: .5rem;
                padding: .65rem;
                outline: none;
                white-space: nowrap;
                font-size: 1.2rem;
                letter-spacing: 1.2px;
                cursor: pointer;
                font-weight: v.$bold;
                text-transform: uppercase;
                color: v.$white;
              }
              .custom-file-input::after {
                display: none;
              }
              .custom-file-input:hover::before {
                background-color: v.$dark;
                border-color: lighten(v.$dark, 7);
                color: v.$white;
              }
            option {
                cursor: pointer;
                &:hover{
                    background-color: darken(v.$greyBlue, 15%);
                }
            }
            select option:checked{
                    background-color: lighten(v.$orangeFlame, 20%);
            }
            .button_trnsp{
                margin-top: 5rem;
                @include m.button(transparent, v.$white, v.$white);
                @include m.tablet {
                    width: 50%;
                    margin: 10rem auto 0;
                }
            }
        }
        .section_content{
            .title{
                font-size: 6rem;
                margin: 10rem 0;
    
                @include m.tablet {
                    font-size: 12rem;
                }
            }
            .section_div{
                p{
                    text-align: center;
                    margin-top: 0;
                    margin-bottom: 3rem;
            
                    @include m.tablet {
                        width: 98%;
                        margin: 0 auto;
                        margin-bottom: 3rem;
                    }
                }
            }
            .g-recaptcha{
                div{
                    margin: auto;
                }
            }
        }
    }
}