@use 'base/variables' as v;
@use 'base/mixins' as m;

.services{
    .hero{
        background-image: linear-gradient(rgb(0, 0, 0), rgba(32, 32, 32, 0.7)),
        url(../../build/gif/services.gif);
        background-size: cover;
        background-position: center center;
        height: 100vh;
        max-width: 100%;
        align-content: center;
        @include m.tablet {
            align-content: end;
        }
        
        .hero_content{
            display: flex;
            gap: .5rem;
            justify-content: center;
            @include m.tablet {
                justify-content: start;
                margin: 0 0 3rem 3rem;
            }
            img{
                display: block;
                margin: 0;
                width: 15%;
                @include m.tablet {
                    width: 15%;
                }
            }
            h1{
                margin: 0;
                text-align: center;
                padding: 0;
                font-size: 6rem;
                line-height: .8;
                letter-spacing: -.2rem;
                color: v.$white;
                font-weight: v.$bold;
                max-width: 100%;
                text-transform: uppercase;
                align-content: center;
                @include m.tablet {
                    margin: auto 0;
                    text-align: start;
                    font-size: 10rem; 
                    letter-spacing: -.5rem;
                }
                @include m.desktop {
                    font-size: 15rem; 
                    letter-spacing: -1rem;
                    justify-content: center;
                }
            }
        }
    }

    .services_section{
        background-image: linear-gradient(rgba(0, 0, 0, .3), rgba(0, 0, 0, 0.8)),
        url(../../build/img/gradient2.webp);
        background-size: cover;

        max-width: 100%;
        align-content: center;
        padding: 5rem 1rem;
        @include m.tablet {
            padding: 15rem 3rem;
        }
        .services_title{
            margin: 0;
            text-align: center;
            margin-bottom: 10rem;
            text-transform: uppercase;
        }
        .services_div{
            display: flex;
            padding: 2rem 0;
            transition: all .3s linear;
            justify-content: space-between;
            @include m.tablet {
                justify-content: space-evenly;
                
            }
            border: none;
            border-radius: 1rem;
            &:hover{
                transform: scale(1.02);
                border: solid .1rem v.$white;
            }
            
            .service{
                flex: 0 0 20%;
                align-content: center;
                display: block;
                .service_title{
                    text-align: center;
                    margin: 0;
                }
                .service_icon{
                    width: 10rem;
                    height: 10rem;
                    display: block;
                    margin: 0 auto;
                    fill: v.$white;
                }
            }
            .service_desc{
                flex: 0 0 65%;
                align-content: center;
            }
            &:nth-of-type(9){
                margin-bottom: 12rem;
            }
        }
        .button_trnsp{
            margin-top: 5rem;
            @include m.button(transparent, v.$white, v.$white);
            @include m.tablet {
                width: 50%;
                margin: 10rem auto 0;
            }
        }
    }
}