@use 'base/variables' as v;
@use 'base/mixins' as m;
.cta{
    .cta_div{
        border-top: solid .1rem v.$white;
        padding: 5rem 1rem 0rem;
        @include m.tablet {
            margin: 0 10rem;
            
        }
        p{
            text-align: center;
            color: v.$white;
            font-family: v.$font_variant;
            font-size: 2.4rem;
            line-height: 1.1;
            font-weight: 100;
            padding: 3rem 0;
    
            @include m.tablet {
                padding: 2rem 0;
                font-size: 3.6rem
            }
        }
    }
    .cta_button{
        .button_trnsp{
            margin-top: 5rem;
            @include m.button(transparent, v.$white, v.$white);
            @include m.tablet {
                width: 50%;
                margin: auto;
            }
        }
        padding-bottom: 10rem;

    }
}